@import url('https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700');
@import url('https://fonts.googleapis.com/css?family=Roboto+Slab:400,700');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400');

.App-header {
  background-color: #38abb4;
  height: 180px;
  padding: 10px;
  color: white;
}
.App-background {
  background-color: #38abb4;
  width: 100%;
  bottom:0px; 
  top: 0px; 
  height: 840px;

}

.header {
  color: #fff;
  background-color: #3b5998;
}

.header2 {
  color: #fff;
  background-color: #dd4b39;
}

.App-logo2 {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 200px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 70px;
}

button {
  border-top: solid 5px rgba(255,255,255);
  border-right: solid 5px rgba(255,255,255);
  border-bottom: solid 2px rgba(255,255,255);
  border-left: solid 5px rgba(255,255,255);
  padding: 9px 12px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #38abb4;
  color: #fff;
  font-weight: 100; 
  float: center;
}

button2 {
  border-top: solid 5px rgba(255,255,255);
  border-right: solid 5px rgba(255,255,255);
  border-bottom: solid 2px rgba(255,255,255);
  border-left: solid 5px rgba(255,255,255);
  padding: 9px 12px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #3B5998;
  color: #fff;
  font-weight: 100; 
  float: center;
}

button:active {
  outline: none;
}
button2:active {
  outline: none;
}
.weather__info {
  padding: 10px 15px;
  width: 60%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
}

.weather__key {
  padding: 10px 15px;
  color: #000000;
  border-bottom: solid 2px rgba(255,255,255,0.06);
  padding: 20px 0 20px 0;
  font-weight: 400;
}

.weather__key:last-child {
  border: 0;
}

.title-container__title {
  font-size: 40px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: 'Roboto Slab', serif;
}

.weather__value {
  color: #38abb4;
  font-weight: 200;
}

.weather__error {
  color: #000000;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200;
}





